<template lang="html">
  <section class="bets-rate">
    <!--  -->
    <el-table
      :data="tableData"
      height="665"
      size="small"
      highlight-current-row
      border
    >
      <el-table-column type="index"></el-table-column>

      <el-table-column
        prop="parentType"
        label=""
        :filters="[
          { text: '', value: 1 },
          { text: '', value: 2 },
          { text: '-', value: 3 },
          { text: '-', value: 4 },
          { text: '-', value: 5 }
        ]"
        :filter-method="filterHandler"
      >
        <template slot-scope="scope">
          <span>{{scope.row.parentType | betType}}</span>
        </template>
      </el-table-column>

      <el-table-column prop="childType" label="" sortable>
        <template slot-scope="scope">
          <span>{{scope.row.childType | rateSecondType}}</span>
        </template>
      </el-table-column>

      <el-table-column prop="betting" label="" sortable>
        <template slot-scope="scope">
          <span>{{scope.row.betting | rateThirdType}}</span>
        </template>
      </el-table-column>

      <el-table-column prop="odds" label="" :width="100">
        <template slot-scope="scope">
          <span>{{scope.row.odds}}</span>
          <span class="pull-right">
            <BaseSetting @on-click="handleSettingRate(scope.row)"/>
          </span>
        </template>
      </el-table-column>
      <el-table-column prop="remark" label=""></el-table-column>
    </el-table>

    <!--  -->
    <el-dialog
      :visible.sync="dialogRateVisible"
      title=""
      width="300px"
    >
      <el-form :model="rateForm">
        <el-form-item>
          <el-input v-model="rateForm.odds" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button type="primary" @click="updateRate"></el-button>
      </span>
    </el-dialog>
  </section>
</template>

<script>
import BaseSetting from '@/components/base/BaseSetting'

export default {
  components: {
    BaseSetting
  },
  data () {
    return {
      tableData: [],
      dialogRateVisible: false,
      rateForm: { odds: '' }
    }
  },
  created () {
    this.fetchBetsRateList()
  },
  methods: {
    // 
    handleSettingRate (obj) {
      Object.assign(this.rateForm, obj)
      this.dialogRateVisible = true
    },
    // 
    updateRate () {
      this.$axios.post('/api-g/oddsset/11xuan5/save', [ this.rateForm ]).then((response) => {
        this.$_.forEach(this.tableData, item => {
          if (item.oddsid === this.rateForm.oddsid) {
            item.odds = this.rateForm.odds
          }
        })
        this.dialogRateVisible = false
        this.$message.success('')
      })
    },
    // 
    filterHandler (value, row, column) {
      const property = column['property']
      return row[property] === value
    },
    fetchBetsRateList (page) {
      this.$axios.get('/api-g/oddsset/11xuan5').then(response => {
        this.tableData = response.data.data
      }).catch(error => console.log(error))
    }
  }
}
</script>

